import { defineStore } from 'pinia'

interface appItem {
  name: string,
  url: string,
  isDesktop: boolean,
  isPinned: boolean,
  isTaskBar: boolean,
  open: boolean,
  mini: boolean,
  hidden: boolean,
  isTop: boolean
}

type appList = appItem[]

interface appState {
  app: appList,
  zIndex: number,
  alwaysTaskBar: string[]
}
interface statusObjType {
  name: string,
  key: string,
  value: boolean
}

type appKey = keyof appItem

/**
   * 
   * {
   *   name 
   *   url  
   *   isDesktop  
   *   isPinned 
   *   isTaskBar 
   *   open  -> (v-if) 
   *   mini 
   *   hidden (v-show) 
   *   isTop 
   * }
   */

const appStore = defineStore({
  id: 'app-store',
  state: (): appState => {
    return {
      app: [{
        name: '',
        url: require('@/assets/icon/appIcon/explorer.png'),
        isDesktop: false,
        isPinned: true,
        isTaskBar: true,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: 'Edge',
        url: require('@/assets/icon/appIcon/edge.png'),
        isDesktop: true,
        isPinned: true,
        isTaskBar: true,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: 'Microsoft Store',
        url: require('@/assets/icon/appIcon/store.png'),
        isDesktop: false,
        isPinned: true,
        isTaskBar: true,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: '',
        url: require('@/assets/icon/appIcon/photos.png'),
        isDesktop: false,
        isPinned: true,
        isTaskBar: false,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: '',
        url: require('@/assets/icon/appIcon/settings.png'),
        isDesktop: false,
        isPinned: true,
        isTaskBar: false,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: '',
        url: require('@/assets/icon/appIcon/alarm.png'),
        isDesktop: false,
        isPinned: true,
        isTaskBar: false,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: '',
        url: require('@/assets/icon/appIcon/bin1.png'),
        isDesktop: true,
        isPinned: false,
        isTaskBar: false,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: 'cortana',
        url: require('@/assets/icon/appIcon/cortana.png'),
        isDesktop: false,
        isPinned: false,
        isTaskBar: false,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: 'Visual Studio Code',
        url: require('@/assets/icon/appIcon/code.png'),
        isDesktop: true,
        isPinned: false,
        isTaskBar: false,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: '',
        url: require('@/assets/icon/appIcon/terminal.png'),
        isDesktop: false,
        isPinned: false,
        isTaskBar: false,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }, {
        name: '',
        url: require('@/assets/icon/appIcon/snake.jpg'),
        isDesktop: true,
        isPinned: true,
        isTaskBar: false,
        open: false,
        mini: false,
        hidden: false,
        isTop: false
      }],
      zIndex: 2,
      alwaysTaskBar: ['', 'Edge', 'Microsoft Store']
    }
  },
  getters: {
    getApp(): appList {
      return this.app
    },
    getTypeApp() {
      return (appType: appKey) => this.app.filter((item) => item[appType])
    },
    getMyApp() {
      return (appName: string) => this.app.filter(item => item.name === appName)[0]
    },
    getZIndex(): number {
      return this.zIndex
    }
  },
  actions: {
    changeZIndex() {
      this.zIndex++
    },
    /**
     * statueObj
     *  - name  app name
     *  - key  key
     *  - value 
     */
    changeAppStatus(statusObj: statusObjType) {
      const { name, key, value } = statusObj
      this.app.forEach((item: appItem) => {
        if (item.name === name) {
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          item[key] = value
          if (key === 'open') {
            item.isTop = value
            if (this.alwaysTaskBar.indexOf(name) === -1) {
              item.isTaskBar = value
            }
            item.mini = false
            item.hidden = false
          } else {
            item.isTop = !item.mini && !item.hidden
          }
        } else {
          if (item.open && key === 'open' && value) {
            item.mini = true
            item.isTop = !item.mini && !item.hidden
          }
        }
      })
    }
  }
})

export {
  appItem,
  appList,
  appStore
}